'use strict';

import angular from 'angular';
import 'lodash';
import boons from './boons.json!'

const boonsCtrlModule = angular.module('boons.controller.js', []);

boonsCtrlModule.filter('unsafe', function($sce) { return $sce.trustAsHtml; })

class BoonsCtrl {

  // called once when the class is instantiated
  constructor ($scope, $http, $filter, $sce, URL) {
    this.$scope = $scope;
    this.URL = URL;
    this.powers = [];
    this.powers = boons;
    this.attributes = [
            'All',
            'Creation',
            'Entropy',
            'Divination',
            'Psychic',
            'Alteration',
            'Spirit',
            'Illusion',
            'Movement',
            'Abjuration',
            'Fortitude',
            'Energy'
          ];
    this.selectedPowers = [];
    this.selectedAttributes = ['All'];
    this.searchQuery = '';
    this.attackAttrSearchText = '';
    this.powerLevelValue = 0;
    this.powerAboveBelow = 'or Greater';
  }

  // public class methods
  updateSearchTextModel (val) {
    this.attackAttrSearchText = val;
  }

  attackAttrSearchFilter () {
    // return all the options if the user hasn't selected a filter
    return (item) => {
      if ( this.attackAttrSearchText === '' ){
        return true;
      } else {
        return item.toLowerCase().match( this.attackAttrSearchText.toLowerCase());
      }
    }.bind(this);
  }

  textSearchFilter () {
    // if `searchQuery` is empty, we want to show all list items
    return (thisPower) => {
      // debugger;
      var item = thisPower;
      if ( this.searchQuery === '' && _.contains( this.selectedAttributes, 'All' ) ) {
        return true;
      }
      var regex = new RegExp(this.searchQuery, 'gi');
      var show = false; // initially we assume that we won't show a given result
      var inAttackFilter = true;
      // now we look for matches in the following object properties
      show = item.name.match(regex) || item.description.match(regex) || item.effect.match(regex);
      // if the selectedAttributes array is longer than 0, we need to show
      // only results that match one of the selected properties
      if ( this.selectedAttributes.length > 0 ) {
        var inAttackFilter = false;
        if ( this.selectedAttributes.indexOf('All') !== -1 ) {
          inAttackFilter = true;
        } else {
          var searchAttrs = angular.copy( this.selectedAttributes );
          var inAttackFilter = _.intersection( item.attribute, searchAttrs ).length > 0;
          // console.log( inAttackFilter );
        }
      }
      // return the result (either `true` or `false`)
      return show && inAttackFilter;
    }
  }

  powerLevelFilter () {
    return (thisPower) => {
      var item = thisPower;
      const powerAbove = ( this.powerAboveBelow === 'or Greater' );
      var include = false;
      for (var i = 0; i <= item.power.length; i++) {
        if ( powerAbove && item.power[i] > this.powerLevelValue ) {
          include = true;
          break;
        } else if ( !powerAbove && item.power[i] <= this.powerLevelValue ) {
          include = true;
          break;
        }
      };
      return include;
    }
  }
}


boonsCtrlModule.controller('BoonsCtrl', ['$scope', 'URL', BoonsCtrl]);

export default boonsCtrlModule;
